@import 'reset';
@import 'variables';

* {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  text-rendering: optimizeLegibility;
}

*:focus {
  outline: none;
}

html {
}

body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  font-family: 'Roboto';
  font-weight: 300;

}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

h5 {
  font-size: 15px;
  font-weight: 700;
  color: white;
  text-align: center;
}

.background-cover {
  background-size: cover;
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #e5e7ed;
}

button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: none;
  box-shadow: 0px 0px;
}

textarea,
input.text,
input[type="text"],
input[type="button"],
input[type="submit"],
.input-checkbox {
  -webkit-appearance: none;
  border-radius: 0;
}

* {
  box-sizing: border-box
}

.green-button {
  color: rgb(248, 248, 248);
  cursor: pointer;
  display: block;
  height: 60px;
  max-width: 90%;
  min-height: 0px;
  min-width: 125px;
  width: 90%;
  background: rgb(54, 192, 96);
  border: 0px none rgb(248, 248, 248);
  border-radius: 100px 100px 100px 100px;
  font-size: $normal-font-size;
  font-weight: 700;
  margin: 20px 0px 15px;
  padding: 0px;
  margin-left: 5%;
  box-sizing: border-box;
  font-family: $font-family
}

.icon-style {
  text-align: left;
  position: absolute;
  margin-left: 20px;
  color: white;
  z-index: 200;
}

@media screen and (max-width: 679px) {

  .container {
    box-sizing: border-box;
    width: 90%;
    margin-left: 5%;
    margin-top: 12%;
    padding-top: 25px;
    padding-bottom: 25px;
    padding-left: 4%;
    padding-right: 4%;
    max-height: 100%;
    max-width: 810px;
    position: relative;
    border: 0px none rgb(27, 36, 51);
    border-radius: 10px 10px 10px 10px;
    background: rgba(35, 51, 75, 0.7);
  }

  .title {
    text-align: center;
    color: rgb(255, 255, 255);
    height: 20px;
    border: 0px none rgb(255, 255, 255);
    font-weight: 700;
    font-size: $normal-font-size;
    margin: 0px 0px 20px;
  }

  .button-container {
    height: 47px;
    margin-top: 30px;

  }

}

@media screen and (min-width: 680px) {

  .container {
    margin-top: 5%;
    box-sizing: border-box;
    width: 50%;
    margin-left: 25%;
    padding-top: 45px;
    padding-bottom: 45px;
    padding-left: 8%;
    padding-right: 8%;
    max-width: 810px;
    max-height: 100%;
    position: relative;
    text-decoration: none solid rgb(27, 36, 51);
    text-size-adjust: 100%;
    background: rgba(35, 51, 75, 0.7);
    border: 0px none rgb(27, 36, 51);
    border-radius: 10px 10px 10px 10px;
  }

  .title {
    box-sizing: border-box;
    color: rgb(255, 255, 255);
    height: 26px;
    font-weight: 700;
    margin: 0px 0px 20px;
    text-align: center;
    font-size: $bigger-font-size
  }

  .button-container {
    height: 47px;
    margin-top: 30px;
  }
}