@import '../../../common-styles/variables';
@import '../../../common-styles/common-style';

.back-button {
  color: rgb(248, 248, 248);
  cursor: pointer;
  display: block;
  height: 47px;
  max-height: 1 e+06px;
  min-height: 0px;
  min-width: 125px;
  text-size-adjust: 100%;
  width: 130px;
  background: $green;
  border: 0px none rgb(248, 248, 248);
  border-radius: 100px 100px 100px 100px;
  font-weight: 700;
  font-size: $small-font-size;
  padding: 0px;
  margin-top: 30px;
  box-sizing: border-box;
  float: left;
}

.next {
  float: right;
}

.question-number {
  color: rgb(255, 255, 255);
  font-weight: 500;
  font-size: $smaller-font-size;
  margin: 0px 0px 14px;
  text-align: left;
}

@media screen and (max-width: 680px) {

  .question-text {
    color: rgb(255, 255, 255);
    font-weight: 500;
    font-size: $big-font-size;
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: left;
  }

}

@media screen and (min-width: 680px) {

  .question-text {
    color: rgb(255, 255, 255);
    font-weight: 500;
    font-size: $big-font-size;
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: left;
  }

}
